*{margin: 0px; padding: 0px ; box-sizing: border-box;}

body{
    background-color: cyan;
    box-sizing: border-box;
}

.title{
    color: rgb(220, 127, 233);
    text-align: center;
    margin: 40px;
    text-shadow: blueviolet;
    font-weight: 700;
    font-family: 'Poppins', sans-serif;
}
.bordercolor{
    justify-content: center;
    align-items: center;
    padding-left: 27%;
}
.bordercolor .borderinner{
    width: 65%;
    height: 70;
    border-radius: 50px;
    background-color: beige;
    align-items: center;
    justify-content: center;
    
}

.imageGrid{
    align-items: center;
    justify-content: center;
    display: flex;
}

.imageGrid img{
    height: 250px;
    width: 250px;
    padding-left: 10px;
    border-color: black;
    border-radius: 25px;
    box-shadow: black;
    
}